
import { defineComponent, reactive, getCurrentInstance } from "vue"
import Titlecom from "../components/titlecom.vue"
import axios from "axios"

export default defineComponent({
  name: "Page2",
  components: {
    Titlecom,
  },

  setup() {
    const { proxy } = getCurrentInstance() as any
    console.log(proxy)
    proxy.$message.success("this is message")

    const formInline = reactive({
      user: "",
      region: "",
    })

    const onSubmit = () => {
      console.log(formInline.user, formInline.region)
      const dd = reactive({
        userName: formInline.user,
        userData: formInline.region,
      })
      if (formInline.user == "") {
        alert("数据为空")
      } else {
        console.log(dd)
        axios({
          url: "/api",
          method: "get",
          timeout: 1000,
          headers: {
            "Content-Type": "application/json",
          },
          data: dd,
          // responseType: "stream",
        })
          .then((res) => {
            console.log("数据：" + res.data.code)
          })
          .catch((err) => {
            console.log("错误：" + err)
          })
      }
    }

    return { formInline, onSubmit }
  },
})
